import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { retrieveFeedbacks } from "../actions/feedbacks"
import Table from 'react-bootstrap/Table';
import {groupBy} from 'rxjs';

class FeedbackDashboard extends Component {
    constructor(props){
        super(props);
        // this.setCurrentBike = this.setCurrentBike.bind(this);
        // this.state = {
        //     currentBike: null,
        //     currentIndex: 0
        // };
    }

    componentDidMount(){
        this.props.retrieveFeedbacks();
    }

    componentDidUpdate(prevProps, prevState){
        // if(prevState.currentBike != this.props.bikes[0])
        // this.props.bikes?.length > 0 && this.setState({currentBike: this.props.bikes[0]});
    }

    // setCurrentBike(bike, index) {
    //     this.setState({
    //         currentBike: bike,
    //         currentIndex: index
    //     });
    // }

    render () {
        // const { currentBike, currentIndex } = this.state;
        const { feedbacks, loginInfo } = this.props;
        if(!loginInfo.isLoggedIn){
          return <Redirect to="/"/>
        }

        return (
        <div className="list row">
        <div className="col-md-6">
          <h4>Feedbacks</h4>

          <Table striped bordered hover>
      <thead>
        <tr>
          <th>#</th>
          <th>Application</th>
          <th>User Id</th>
          <th>Page URL</th>
          <th>Page Name</th>
          <th>Feedback</th>
          <th>Page Rating</th>
          <th>Category</th>
          <th>Images</th>
          <th>Received Date</th>
        </tr>
      </thead>
      <tbody>
      {feedbacks &&
              feedbacks.map((feedback, index) => (
                <tr>
                <td>{index + 1}</td>
                <td>{feedback.projectName}</td>
                <td>{feedback.userId}</td>
                <td>{feedback.pageUrl}</td>
                <td>{feedback.pageName}</td>
                <td>{feedback.feedbackMessage}</td>
                <td>{feedback.rating}</td>
                <td>Performance</td>
                <td><a src={feedback.image}>image</a></td>
                <td>{feedback.createdOn}</td>
              </tr>
              ))}
      </tbody>
    </Table>
          </div>

          {/* <div className="col-md-6">
          {currentBike && (
            <div>
              <h4>Bike</h4>
              <div>
                <label>
                  <strong>Name:</strong>
                </label>{" "}
                {currentBike.productName}
              </div>
              <div>
                <label>
                  <strong>Brand:</strong>
                </label>{" "}
                {currentBike.brand.brandName}
              </div>
              <div>
                <label>
                  <strong>Category:</strong>
                </label>{" "}
                {currentBike.category.categoryName}
              </div>
            </div>
          )}
        </div> */}
        <div>
        {pieGraphData(feedbacks)}
        </div>
        </div>
        );
    }
}

const pieGraphData = (feedbacks) => {
 var map = new Map();
 feedbacks.groupBy(p => p.CategoryId)
//  feedbacks.map(x => {
//     map.set(x.CategoryId, feedbacks.groupBy(p => p.CategoryId))
//  })
}

const mapStateToProps = (state) => {
    return {
        feedbacks: state.feedbacks,
        loginInfo: state.login
    };
};

export default connect(mapStateToProps, {
     retrieveFeedbacks
})(FeedbackDashboard);